
html, body, #app {
  height: 100%;
}
@font-face {
  font-family: 'zurich';
  src: url(./static/fonts/zurich.7cda345.ttf);
}
.top {
  width: 100%;
  height: 100%;
  display: grid;
  background-color: black;
  grid-template-rows: 45px 1fr;
  grid-template-columns: repeat(3, 1fr);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  font-size: 12px;
  font-family: Arial,Helvetica,sans-serif;
  letter-spacing: .05em;
}
.top__header {
    grid-row: 1;
    grid-column: 2;
    border-left: 1px solid black;
    border-right: 1px solid black;
    text-decoration: none;
    background-color: white;
    color: black;
}
.top__header__logo {
      position: relative;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      font-size: 22px;
      font-family: zurich;
      font-weight: bold;
      letter-spacing: .1em;
}
.top__contents {
    grid-row: 2;
    grid-column: 1 / 4;
    border-top: 1px solid black;
}

